.title {
	@include verdana;
	@include f(44px, 700, 1.23);
	color: $black;
	letter-spacing: normal;
	@include md {
		font-size: 25px;
		line-height: 1.33; } }
.title-lg {
	@include verdana;
	@include f(44px, 700, 1.22);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 30px;
		line-height: 1.33; } }
.title-m {
	@include verdana;
	@include f(30px, 700, 1.8);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 25px;
		line-height: 1.24; } }
.title-sm {
	@include verdana;
	@include f(21px, 700, 1.48);
	color: $black;
	letter-spacing: normal;
	@include sm {
		font-size: 19px; } }

.subtitle {
	@include verdana;
	@include f(18px, 700, 1.23);
	color: $text;
	letter-spacing: normal; }

.text-center {
	text-align: center; }

.article,
article {
	p {
		line-height: 1.87;
		letter-spacing: .013em;
		&:not(:first-child) {
			margin-top: 30px; } }
	h1 {
		&:not(:last-child) {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 18px; } } } }

