.video {
	position: relative;
	@include hover {
		.play {
			transform: translate(-50%, -50%) scale(1.2); } }
	&__play {
		@include vcenter; }
	&__item {
		display: block;
		@include cover;
		border-radius: 10px;
		overflow: hidden;
		height: 450px;
		.my-course & {
			@include sm {
				border-radius: 0; } }
		@include xs {
			height: 260px; } } }
